import axios from 'axios'
import { axiosHelper } from './api-config.js'
import http from './http.js'
//包装param参数
// 声明基础访问地址
// axios.defaults.baseURL = 'http://localhost:8082'

//声明一个调用方法
/*export const requestLogin = () => {
  return axios.get('/demo/users/listuser').then(res => res.data.userList)
}*/

let instance = axiosHelper.createAxios({
  baseURL: '',
  timeout: 30000
});



export default {
  login(userInfo) {
    // let url = 'login';
    let url="login"
    console.log(userInfo)
    // return instance.post(url, { email: email, password: password });
    return http.get(url,userInfo);
  },
  changeToken(id) {
  	let url = "/use_case_asset/" + id;
  	return instance.get(url, id);
  }
}



