<template>
  <el-form :model="user" :rules="rules" ref="loginForm" label-position="left" label-width="0px" 
  class="demo-ruleForm login-container">
    <h3 class="title">系统登录</h3>
    <el-form-item prop="name">
      <el-input type="text" v-model="user.userId" auto-complete="off" placeholder="账号"></el-input>
    </el-form-item>
    <el-form-item prop="pass">
      <el-input type="password" v-model="user.password" auto-complete="off" placeholder="密码"></el-input>
    </el-form-item>
    <el-checkbox v-model="checked" checked class="remember">记住密码</el-checkbox>
    <el-form-item style="width:100%;">
      <el-button type="primary" style="width:100%;" @click="login">登录</el-button>
      <!--<el-button @click.native.prevent="handleReset2">重置</el-button>-->
    </el-form-item>
  </el-form>
</template>
<script>
  import Account from '../../api/account.js'
  export default {
    data () {
      return {
        user: {
          userId:'',
          password:''
        },
        rules: {
          name: [
          {required: true, message: '用户名不能为空', trigger: 'blur'}
          ],
          pass: [
          {required: true, message: '密码不能为空', trigger: 'blur'}
          ]
        },
        checked: true
      }
    },
    methods: {
      login () {
        Account.login(this.user).then(res=>{
          console.log(1)
          console.log(res)
        },err=>{
          console.log(2)
          console.log(err)
        }).catch(err=> {
          console.log(3)
          console.log(err)
        })
        // this.$refs.loginForm.validate((valid) => {
        //   if (valid) {
        //     if (this.user.name === 'admin' && this.user.pass === '123') {
        //       this.$notify({
        //         type: 'success',
        //         message: '欢迎你,' + this.user.name + '!',
        //         duration: 3000 
        //       })
        //       window.sessionStorage.setItem('token',true)
        //       this.$router.push({name:'Home'});
        //     } else {
        //       this.$message({
        //         type: 'error',
        //         message: '用户名或密码错误',
        //         showClose: true
        //       })
        //     }
        //   }
        //   else {
        //     return false
        //   }
        // })
      }
    },
  }
</script>
<style src="./login.css" lang="scss" scoped></style>






